import React from 'react'

import Phone from '../citybank/Phone'
import PhoneContent from '../citybank/PhoneContent'
import SmallTablet from '../citybank/SmallTablet'
import Couch from '../citybank/Couch'

const CityBank = () => {
  return (
    <div className='bg-slate-50 mx-2 rounded-3xl flex justify-center border-sudokuBoard4 border-4 items-center' style={{height: 642, width: 1288, overflow: 'hidden', paddingTop: 12}}>
        

        <div className=' flex items-center justify-center text-rouLan' style={{height: 542, width: 588, marginLeft: 210}}>
            <div>
            <div className='flex font-bold font-quickColour1 text-3xl' style={{marginBottom: 2}}>
                Banking is easy.
            </div>
            <div className='font-bold font-quickColour1 text-3xl' style={{marginBottom: 80}}>
                Even easier on our
                <div className='flex'>
                    <div className='mt-1' style={{font: "bold 28px Aldrich, sans-serif"}}>
                    City Bank
                    </div>
                    <div>
                    &nbsp;app.
                    </div>
                </div>
                
            </div>
            
            <div className='flex justify-center'>
                <Couch/>
            </div>  
            </div>
        </div>


        <div className='phone-scale' style={{marginLeft: 50, marginRight: -100}}>
            <Phone
                innerMarginLeft = {16}
                innerMarginRight = {16}
                innerContent={<PhoneContent/>}
            />
        </div>

        <div className='phone-scale' style={{marginLeft: 6, marginRight: 120}}>
            <SmallTablet/>
        </div>

    </div>
  )
}

export default CityBank
