import React from 'react'

import Phone from '../citybank/Phone'
import PhoneContent from '../citybank/PhoneContent'
import SmallTablet from '../citybank/SmallTablet'
import Couch from '../citybank/Couch'

const CityBankMobile = () => {
  return (
    <div className='bg-white mx-2 rounded-3xl flex justify-center items-center border-slate-200 border-2' style={{height: 352, width: 636, overflow: 'hidden'}}>
        <div className='flex justify-center items-center' style={{height: 352, width: 296}}>
            <div className='eighty-scale ml-8'>
                <div className='flex font-bold font-quickColour1 text-3xl' style={{marginBottom: 2}}>
                    Banking is easy.
                </div>
                <div className='font-bold font-quickColour1 text-3xl' style={{marginBottom: 80}}>
                    Even easier on our
                    <div className='flex'>
                        <div className='mt-1' style={{font: "bold 28px Aldrich, sans-serif"}}>
                        City Bank
                        </div>
                        <div>
                        &nbsp;app.
                        </div>
                    </div>
                    
                </div>
                
                <div className='flex justify-center'>
                    <Couch/>
                </div>  
            </div>

        </div>

        <div className='flex justify-center items-center fifty-scale' style={{height: 352, width: 160}}>

            <div className='fifty-scale' style={{marginLeft: 0, marginRight: 0}}>
                <Phone
                    innerMarginLeft = {16}
                    innerMarginRight = {16}
                    innerContent={<PhoneContent/>}
                />
            </div>

        </div>
        <div className='flex justify-center items-center fifty-scale' style={{height: 352, width: 180, marginLeft: -40}}>
            <div className='fifty-scale'>
                <SmallTablet/>
            </div>
        </div>
    </div>
  )
}

export default CityBankMobile
